 echten Kontakt, erfahren die anderen\nund sich selbst als individuelle Charaktere, die sich von Auftritt zu Auftritt\nmusikalisch wie persönlich weiterentwickeln, verändern, ständig von einer anderen\nSeite zeigen und im Grunde doch für immer die gleichen bleiben werden.\n    ")]),_vm._v(" "),_c('div',{staticClass:"member left"},[_c('img',{attrs:{"src":"https://preview.ibb.co/grckvJ/jojo.png"}}),_vm._v(" "),_c('p',[_vm._v("Jojo Mann (Gitarre) kommt ursprünglich aus dem Stuttgarter Raum, für sein Studium zog es ihn bereits mit 18 Jahren nach Mannheim zu Prof. Frank Kuruc und 2017 dann zu Hanno Busch nach Berlin. Er ist für seinen sorgfältig erarbeiteten Sound und seine fesselnden Soli bekannt und beliebt und spielt seit März 2018 im Bundesjugendjazzorchester (BuJazzO).")])]),_vm._v(" "),_c('div',{staticClass:"member right"},[_c('img',{attrs:{"src":"https://preview.ibb.co/mth45J/bene.png"}}),_vm._v(" "),_c('p',[_vm._v("Bene Jäckle (Tenorsaxophon) aus Waghäusel bei Bruchsal studiert seit Oktober 2015 in Mannheim, sein musikalischer Werdegang nahm im Landesjugendjazzorchester RLP entscheidende Fortschritte. Zu Beginn des Studiums wechselte er vom Alt- auf’s Tenorsaxophon und spielt seitdem in zahlreichen Mannheimer Bands als gefragter und beliebter Sideman. Ab Februar 2018 verbrachte er ein Auslandssemester in Salvador de Bahia in Brasilien.")])]),_vm._v(" "),_c('div',{staticClass:"member left"},[_c('img',{attrs:{"src":"https://preview.ibb.co/d7V2sy/paul.png"}}),_vm._v(" "),_c('p',[_vm._v("Paul Janoschka (Klavier) steht bereits am Ende seines Studiums, in Mannheim studierte er zuerst bei Prof. Joerg Reiter und seit 2015 bei Prof. Rainer Böhm. Er gehört im Rhein-Neckar Raum über die Szene der Mannheimer Hochschule hinaus zu den aktivsten Musikern seiner Generation, ist regelmäßig aber auch mit älteren Musikern zu hören, so zum Beispiel bei der Kicks & Sticks Big Band. Paul Janoschka ist Preisträger des Yamaha Jazz Piano Wettbewerbs 2016 und Stipendiat der Yehudin